.main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 55px;

}

.main-heading {
    font-family: cursive;
    font-size: 27px;
    color: #051c33;
}

.search {
    /* padding-left: 10px; */

}

input {
    width: 392px;
    padding: 10px 15px;
    background-color: #edeff0;
    border: 3px solid #d7dbdf;
    font-size: 17px;
    border-right: none;
    border-radius: 5px 0 0 5px;
    outline: none;
}



.btn-search {
    padding: 15px 17px 14px 22px;
    border: none;
    border-radius: 0 4px 4px 0;
    margin-left: 0.8px;
    color: white;
    cursor: pointer;
    width: 58px;

}

.dark {
    background-color: #051c33;
}

.light {
    background-color: rgb(247, 247, 247);
}

.btn {
    margin-top: 35px;
    display: flex;
    justify-content: space-evenly;

}



.btns {
    padding: 2px 27px 3px 22px;
    background-color: #051c33;
    color: #fff;
    margin: 10px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 16px;
    border-radius: 3px;

}

.pictures {
    margin-top: 15px;
    margin-bottom: 10px;
    text-transform: capitalize;
    color: #051c33;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 25px;


}

.image-card {
    display: flex;
    width: 65%;
    flex-flow: wrap;
    row-gap: 10px;
    gap: 20px
}

.image-one {
    width: 226px;
    height: 162px;




}